@import url(../scss/vendor/swiper-bundle.min.css);
@import url(../../node_modules/simplebar/dist/simplebar.min.css);
:root {
  --color-black: #000;
  --color-white: #fff;
  --color-dark: #151515;
  --quill-grey: #e5e5e3;
  --color-grey: #858585;
  --color-alto: #d7d7d7;
  --color-tundora: #454545;
}

@font-face {
  font-family: 'Montserrat';
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: 'Montserrat';
  src: url("../fonts/Montserrat-Medium.woff2") format("woff2");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: 'Montserrat';
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}

