html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  overflow-anchor: none;
  margin: 0;
  font-family: "Montserrat", sans-serif;
  font-weight: 500;
  background-color: var(--color-white);
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.is-outer {
  position: relative !important;
  left: -9999px !important;
}

.site-container {
  overflow: hidden;
}

.stop-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
}

.is-hidden {
  display: none !important;
}

.for-mobile {
  display: none;
}

@media (max-width: 1024px) {
  .for-mobile {
    display: block;
  }
}

.error {
  border: 2px #ff4019 solid !important;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.container {
  margin: 0 auto;
  padding: 0 40px;
}

@media (max-width: 568px) {
  .container {
    padding: 0 15px;
  }
}

.container-narrow {
  max-width: 1406px;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.main-link {
  position: relative;
  font-weight: 500;
  font-size: 15px;
  color: var(--color-dark);
}

.main-link::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: -5px;
  width: 100%;
  height: 2px;
  background-color: currentColor;
  opacity: 0;
  transition: opacity 0.15s;
}

.main-link:hover::before {
  opacity: 1;
  transition: opacity 0.15s;
}

.main-link--white {
  color: var(--color-white);
}

.quantity {
  display: inline-flex;
  align-items: center;
  border-radius: 25px;
  padding: 0 5px;
  height: 15px;
  font-weight: 700;
  font-size: 10px;
  color: var(--color-white);
  background-color: var(--color-dark);
}

.to-top {
  position: fixed;
  display: inline-block;
  right: 2%;
  bottom: 5%;
  align-items: center;
  justify-content: center;
  border-radius: 100%;
  padding: 14px 18px;
  width: 48px;
  height: 48px;
  box-shadow: 0 5px 30px rgba(0, 0, 0, 0.1);
  background-color: var(--color-white);
}

.header {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 100;
  width: 100%;
}

.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 36px;
  padding-bottom: 36px;
}

@media (max-width: 568px) {
  .header__container {
    padding: 20px 15px;
  }
}

.header__shop-nav {
  margin-left: 254px;
}

@media (max-width: 1200px) {
  .header__shop-nav {
    margin-left: 0;
  }
}

.logo {
  display: inline-block;
  width: 117px;
  height: 22px;
}

.logo svg {
  display: block;
  width: 100%;
  height: 100%;
  fill: var(--color-dark);
  object-fit: cover;
}

@media (max-width: 568px) {
  .logo {
    width: 90px;
    height: 17px;
  }
}

.catalog-header {
  padding-top: 38px;
}

.card-page .header {
  padding-top: 0;
}

@media (max-width: 1200px) {
  .nav {
    position: fixed;
    left: -150vw;
    top: 0;
    padding: 10% 0;
    width: 60%;
    height: 100vh;
    min-height: 410px;
    background-color: #fff;
    transition: left 0.3s;
  }
}

@media (max-width: 768px) {
  .nav {
    padding: 20% 0;
    width: 80%;
  }
}

@media (max-height: 440px) {
  .nav {
    padding: 0;
  }
}

.burger {
  position: relative;
  display: none;
  width: 25px;
  height: 18px;
}

.burger::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 2px;
  background-color: var(--color-dark);
}

.burger::after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 2px;
  background-color: var(--color-dark);
}

.burger__line {
  position: absolute;
  left: 0;
  top: 50%;
  width: 100%;
  height: 2px;
  background-color: var(--color-dark);
  transform: translateY(-50%);
}

@media (max-width: 1200px) {
  .burger {
    display: block;
  }
}

.nav__list {
  display: flex;
}

@media (max-width: 1200px) {
  .nav__list {
    display: block;
  }
}

@media (max-width: 1200px) {
  .nav__item {
    margin-right: 0;
  }
}

.nav__item:not(:last-child) {
  margin-right: 42px;
}

@media (max-width: 1200px) {
  .nav__item:not(:last-child) {
    margin-right: 0;
  }
}

@media (max-width: 1200px) {
  .nav__link {
    display: inline-block;
    border-bottom: 1px solid #bfbfbf;
    padding: 30px 10%;
    width: 100%;
    font-size: 18px;
  }
}

@media (max-width: 1200px) {
  .nav__link:hover::before {
    opacity: 0;
  }
}

.nav__link--current::before {
  opacity: 1;
  transition: opacity 0.15s;
}

@media (max-width: 1200px) {
  .nav__link--current::before {
    opacity: 0;
  }
}

.nav--visible {
  left: 0;
  transition: left 0.3s;
}

.nav__close {
  display: none;
}

@media (max-width: 1200px) {
  .nav__close {
    position: absolute;
    right: 10%;
    top: 6%;
    display: flex;
  }
}

.nav__close svg {
  margin-left: 10px;
  width: 20px;
  height: 15px;
}

.shop-nav {
  display: flex;
  align-items: center;
}

.shop-nav__item:not(:last-child) {
  margin-right: 25px;
}

@media (max-width: 568px) {
  .shop-nav__item:not(:last-child) {
    margin-right: 15px;
  }
}

.shop-nav__link {
  display: inline-block;
  width: 16px;
  height: 16px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  transition: opacity 0.3s;
}

.shop-nav__link:hover {
  opacity: 0.6;
  transition: opacity 0.3s;
}

.shop-nav__link--user {
  background-image: url("../img/user.png");
}

.cart {
  width: auto;
  height: auto;
  display: flex;
}

.cart__icon {
  display: inline-block;
  width: 16px;
  height: 16px;
  background-image: url("../img/bag.png");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  margin-right: 3px;
}

.search-form__btn {
  display: inline-block;
  width: 16px;
  height: 16px;
  background-image: url("../img/search.png");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  transition: opacity 0.3s;
}

.search-form__btn:hover {
  opacity: 0.6;
  transition: opacity 0.3s;
}

.hero {
  position: relative;
  height: 100vh;
  max-height: 1080px;
  min-height: 700px;
  background-color: var(--quill-grey);
  background-image: url("../../img/pexels-ovan-62693.jpg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.hero::before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.05);
}

.hero__container {
  position: relative;
  height: 100%;
}

.hero__title {
  margin: 0;
  margin-bottom: 45px;
  max-width: 570px;
  font-weight: 700;
  font-size: 72px;
}

@media (max-width: 1200px) {
  .hero__title {
    max-width: 500px;
    font-size: 50px;
  }
}

@media (max-width: 568px) {
  .hero__title {
    font-size: 45px;
  }
}

.hero__descr {
  margin: 0;
  font-size: 18px;
}

@media (max-width: 1200px) {
  .hero__descr {
    font-size: 14px;
  }
}

.hero__image {
  position: absolute;
  right: 150px;
  bottom: 0;
  max-height: 90%;
}

@media (max-width: 1600px) {
  .hero__image {
    right: 50px;
    width: 480px;
  }
}

@media (max-width: 1200px) {
  .hero__image {
    right: 0;
    width: 350px;
  }
}

@media (max-width: 768px) {
  .hero__image {
    display: none;
  }
}

.hero__content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  padding: 0 250px;
  height: 100%;
}

@media (max-width: 1600px) {
  .hero__content {
    padding: 0 100px;
  }
}

@media (max-width: 1200px) {
  .hero__content {
    padding: 0;
  }
}

@media (max-width: 1600px) {
  .hero {
    height: 700px;
  }
}

@media (max-width: 1200px) {
  .hero {
    height: 600px;
    min-height: 600px;
  }
}

.banner-slider {
  overflow: hidden;
  width: 100%;
  height: 100%;
}

.banner-slider .swiper-slide {
  height: 100%;
}

.banner-pag,
.catalog-pag {
  left: 50%;
  margin-bottom: 27px;
  transform: translateY(-50%);
}

.banner-pag .swiper-pagination-bullet,
.catalog-pag .swiper-pagination-bullet {
  margin: 0 12px !important;
  width: 6px;
  height: 6px;
  background-color: var(--color-white);
  opacity: 0.5;
}

.banner-pag .swiper-pagination-bullet-active,
.catalog-pag .swiper-pagination-bullet-active {
  position: relative;
  background-color: var(--color-dark);
  opacity: 1;
}

.banner-pag .swiper-pagination-bullet-active::before,
.catalog-pag .swiper-pagination-bullet-active::before {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  z-index: -1;
  border-radius: 100%;
  width: 20px;
  height: 20px;
  background-color: var(--color-white);
  transform: translate(-50%, -50%);
}

@media (max-width: 768px) {
  .banner-pag,
  .catalog-pag {
    margin-bottom: 3px;
  }
}

.catalog-pag {
  transform: translate(-50%, -50%);
}

.marketing {
  position: absolute;
  left: 40px;
  bottom: 50px;
  z-index: 10;
  display: flex;
  padding: 9px;
  width: 280px;
  box-shadow: 0 5px 30px rgba(0, 0, 0, 0.1);
  background-color: var(--color-white);
  transform: translateX(-150vw);
  transition: transform 0.3s;
}

.marketing--visible {
  transform: translateX(0);
}

.marketing__image {
  flex-shrink: 0;
  width: 70px;
  height: 90px;
}

.marketing__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.marketing__text {
  margin-left: 10px;
}

.marketing__descr {
  margin: 0;
  font-size: 11px;
  color: var(--color-grey);
}

.marketing__title {
  margin: 3px 0;
  padding-right: 15px;
  min-height: 34px;
  font-size: 15px;
}

.marketing__where-from {
  display: inline-block;
  margin: 0;
  font-size: 11px;
  line-height: 1.2;
  color: var(--color-grey);
}

.marketing__close {
  position: absolute;
  right: 10px;
  top: 10px;
  z-index: 20;
  width: 10px;
  height: 10px;
  background-image: url("../img/close.png");
  background-position: center;
  background-repeat: no-repeat;
  background-color: transparent;
}

@media (max-width: 568px) {
  .marketing {
    left: 15px;
  }
}

.banners__container {
  padding-top: 50px;
}

.banners__list {
  display: flex;
}

@media (max-width: 1200px) {
  .banners__list {
    flex-wrap: wrap;
  }
}

.banners__item {
  width: 33%;
  background-color: var(--color-alto);
}

.banners__item:not(:last-child) {
  margin-right: 0.6%;
}

@media (max-width: 1200px) {
  .banners__item {
    margin-right: 0;
    margin-bottom: 20px;
    width: 100%;
  }
}

.banners-article {
  padding-top: 55px;
  padding-left: 60px;
  min-height: 290px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.banners-article--midseason {
  background-image: url("../img/banner11.jpg");
}

.banners-article--summer {
  background-image: url("../img/banner2.jpg");
}

.banners-article--accessories {
  background-image: url("../img/banners__accessories.jpg");
}

.banners-article__title {
  margin: 0;
  padding-right: 60%;
  padding-bottom: 20px;
  font-size: 36px;
}

@media (max-width: 1200px) {
  .banners-article__title {
    padding-right: 30%;
  }
}

@media (max-width: 1200px) {
  .banners-article {
    padding-top: 50px;
    padding-left: 20px;
    background-position: 0 0;
  }
}

.main-products {
  margin-bottom: 50px;
}

.main-products__container {
  padding-top: 105px;
  padding-bottom: 50px;
}

@media (max-width: 568px) {
  .main-products__container {
    padding-top: 70px;
  }
}

.main-products__nav {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 45px;
}

.main-products__item:not(:last-child) {
  margin-right: 36px;
}

@media (max-width: 568px) {
  .main-products__item:not(:last-child) {
    margin-right: 20px;
  }
}

.main-products__btn {
  font-size: 24px;
  color: var(--color-grey);
}

.main-products__btn--current {
  color: var(--color-dark);
}

.main-products__btn--current::before {
  opacity: 1;
  transition: opacity 0.15s;
}

.main-products__btn:hover {
  color: var(--color-dark);
}

@media (max-width: 768px) {
  .main-products__btn {
    font-size: 20px;
  }
}

@media (max-width: 568px) {
  .main-products__btn {
    font-size: 16px;
  }
}

.main-products__list {
  display: flex;
  flex-wrap: wrap;
}

.main-products__btn-center {
  text-align: center;
}

.main-products__btn-center svg {
  transition: transform 0.3s;
